<template>
	<div id="assign-Record">
		<public-header title="排队领取记录"></public-header>
		<div class="content">
			<div class="record-top">
				<div class="record-header">桌号分类</div>
				<div class="record-header">编号</div>
				<div class="record-header">状态</div>
				<div class="record-header c-gray">操作</div>
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="record-list"
			>
				<div class="record-item border-1px-b" v-for="(item, index) in records.data">
					<div class="record-title c-default">{{queue_title}}</div>
					<div class="record-title c-default">{{item.number}}</div>
					<div class="record-title" v-if="item.status == 1">排队中前面还有{{item.before_num}}人</div>
					<div class="record-title" v-else-if="item.status == 2">已入号</div>
					<div class="record-title" v-else-if="item.status == 3">已过号</div>
					<div class="record-title" v-else-if="item.status == 4">已取消</div>
					<div class="record-title c-gray">
						<span class="icon icon-delete" @click="onChangeDelete(item, index)"></span>
					</div>
				</div>
				<div class="loaded" v-if="records.finished">
					<div class="loaded-tips">没有更多了</div>
				</div>
			</van-list>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>暂无排队领取记录!</p>
			</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				id: 0,
				queue_title: '',
				isRefresh: false,
				showPreLoading: true,
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
			}
		},
		methods: {
			onLoad(force){
				var that = this;
				getRecords({
					vue: this,
					force: force,
					data: {
						id: that.id
					},
					url: 'manage/order/assign/record',
					success: (res) => {
						that.queue_title = res.queue_title;
					}
				});
			},
			onChangeDelete(item, index){
				var that = this;
				that.util.jspost({
					url: 'manage/order/assign/del',
					data: {
						id: item.id
					},
					confirm: '确定删除吗？',
					success: function(){
						that.records.data.splice(index, 1);
					}
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			}
		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = parseInt(this.$route.query.id)
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#assign-Record .content .record-top{
		position: fixed;
		top: 46px;
		left: 0;
		right: 0;
		display:flex;
		justify-content:flex-start;
		align-items:center;
		padding:10px;
		background-color:#e5e5e5;
	}
	#assign-Record .content .record-top .record-header{
		width:22%;
		font-size:14px;
		font-weight:bold;
		text-align:right;
	}
	#assign-Record .content .record-top .record-header:first-child{
		width:34%;
		text-align:left;
	}
	#assign-Record .content .van-pull-refresh{
		padding-top: 34px;
	}
	#assign-Record .content .van-pull-refresh__head{
		height: 34px;
		top: -34px;
	}
	#assign-Record .content .record-item{
		padding: 10px;
		background-color: #fff;
		display:flex;
		justify-content:flex-start;
		align-items:center;
	}
	#assign-Record .content .record-item .record-title{
		font-size:14px;
		text-align:right;
	}
	#assign-Record .content .record-item .record-title:first-child{
		width:34%;
		text-align:left;
	}
	#assign-Record .content .record-item .record-title:nth-child(2){
		width:34%;
		text-align:center;
	}
	#assign-Record .content .record-item .record-title:nth-child(3){
		width:18%;
		text-align:center;
		color:#33aafc;
	}
	#assign-Record .content .record-item .record-title:last-child{
		width:13%;
		text-align:right;
	}
	#assign-Record .content .record-item .record-title:last-child .icon{
		font-size: 25px;
	}
	#assign-Record .content .record-item:first-child{
		border-top: 1px solid #e0e0e0
	}
</style>


